<template>
  <section class="loginContainer">
    <div class="loginInner">
      <div class="login_header">
        <!--        <h2 class="login_logo">登陆界面</h2>-->
        <div class="login_header_title">
          <a href="javascript:;" :class="{on: loginWay}" @click="loginWay = true">短信登录</a>
          <a href="javascript:;" :class="{on: !loginWay}" @click="loginWay = false">密码登录</a>
        </div>
      </div>
      <div class="login_content">
        <form @submit.prevent="login">
          <div :class="{on: loginWay}">
            <section class="login_message">
              <input type="text" maxlength="11" placeholder="手机号" v-model="phone">
              <button :disabled="!rightPhone" class="get_verification"
                      :class="{rightPhone: rightPhone && count_down == 0}"
                      @click.prevent="getCode">
                {{count_down > 0 ? `已发送${count_down}s` : '获取验证码'}}
              </button>
            </section>
            <section class="login_verification">
              <input type="text" maxlength="6" placeholder="验证码" v-model="code">
            </section>
            <section class="login_hint">
              温馨提示：未注册硅谷外卖帐号的手机号，登录时将自动注册，且代表已同意
              <a href="javascript:;">《用户服务协议》</a>
            </section>
          </div>
          <div :class="{on: !loginWay}">
            <section>
              <section class="login_message">
                <input type="text" maxlength="11" placeholder="手机/邮箱/用户名" v-model="name">
              </section>
              <section class="login_verification">
                <input type="text" max-length="8" placeholder="密码" v-model="pwd" v-if="showPwd">
                <input type="password" maxlength="8" placeholder="密码" v-model="pwd" v-else>
                <div class="switch_button" :class="showPwd ? 'on' : 'off'" @click="showPwd = !showPwd">
                  <div class="switch_circle" :class="{right: showPwd}"></div>
                  <span class="switch_text">{{showPwd ? 'abc' : '...'}}</span>
                </div>
              </section>
              <section class="login_message">
                <input type="text" maxlength="4" placeholder="验证码" v-model="captcha">
                <img class="get_verification" src="http://localhost:4000/captcha" alt="captcha" ref="getCaptcha" @click="getCaptcha">
              </section>
            </section>
          </div>
          <button class="login_submit">登录</button>
        </form>
        <a href="javascript:;" class="about_us">关于我们</a>
      </div>
      <a href="javascript:" class="go_back" @click="$router.back()">
        <i class="iconfont iconicon_arrow_left"></i>
      </a>
    </div>
  </section>
</template>

<script>
import {MessageBox} from 'mint-ui'
import {reqShortMsg, reqPhoneLogin, reqNameLogin} from '../../api'

export default {
  data () {
    return {
      loginWay: true, // 登录方式
      count_down: 0, // 验证码获取倒计时
      showPwd: false, // 显示密码
      phone: '', // 手机号
      code: '', // 手机验证码
      name: '', // 用户名
      pwd: '', // 密码
      captcha: '' // 图形验证码
    }
  },
  methods: {
    async login () {
      let result
      if (this.loginWay) { // 短信登录
        const {phone, code} = this
        if (!this.rightPhone) {
          MessageBox('提示', '请输入正确的手机号')
          return
        } else if (!/^\d{6}$/.test(code)) {
          MessageBox('提示', '请输入正确的验证码')
          return
        }
        result = await reqPhoneLogin(phone, code)
      } else { // 用户名登录
        const {name, pwd, captcha} = this
        if (!name) {
          MessageBox('提示', '请输入正确的用户名')
          return
        } else if (!pwd) {
          MessageBox('提示', '请输入正确的密码')
          return
        } else if (!captcha) {
          MessageBox('提示', '请输入正确的验证码')
          return
        }
        result = await reqNameLogin({name, pwd, captcha})
      }
      if (result.code === 0) { // 登录成功
        // 保存登录信息
        const user = result.data
        this.$store.dispatch('recordUser', user)
        // 登录成功去个人中心界面
        this.$router.replace('/personal')
      } else if (result.code === 1) { // 登录失败
        MessageBox('提示', result.msg)
        this.getCaptcha()
      }
    },
    async getCode () {
      if (!this.count_down) {
        this.count_down = 30
        this.timer = setInterval(() => {
          this.count_down--
          if (this.count_down <= 0) {
            clearInterval(this.timer)
          }
        }, 1000)
        const result = await reqShortMsg(this.phone)
        if (result.code === 0) {
          console.log(result)
        } else if (result.code === 1) {
          MessageBox('提示', result.msg)
          this.count_down = 0
          clearInterval(this.timer)
        }
      }
    },
    getCaptcha () {
      this.$refs.getCaptcha.src = 'http://localhost:4000/captcha?' + Date.now()
    }
  },
  mounted () {
    this.getCaptcha()
  },
  computed: {
    rightPhone () {
      return /^1\d{10}$/.test(this.phone)
    }
  }
}
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import '../../common/stylus/mixin.styl'
  .loginContainer
    width 100%
    height 100%
    background #fff
    .loginInner
      padding-top 60px
      width 80%
      margin 0 auto
      .login_header
        .login_logo
          font-size 32px
          font-weight bold
          color #02a774
          text-align center
        .login_header_title
          padding-top 40px
          text-align center
          >a
            color #333
            font-size 14px
            padding-bottom 4px
            &:first-child
              margin-right 40px
            &.on
              color #02a774
              font-weight 700
              border-bottom 2px solid #02a774
      .login_content
        >form
          >div
            display none
            &.on
              display block
            input
              width 100%
              height 100%
              padding-left 10px
              box-sizing border-box
              border 1px solid #ddd
              border-radius 4px
              outline 0
              font 400 14px Arial
              &:focus
                border 1px solid #02a774
            .login_message
              position relative
              margin-top 16px
              height 48px
              font-size 14px
              background #fff
              .get_verification
                position absolute
                top 50%
                right 10px
                transform translateY(-50%)
                border 0
                color #ccc
                font-size 14px
                background transparent
                &.rightPhone
                  color black
            .login_verification
              position relative
              margin-top 16px
              height 48px
              font-size 14px
              background #fff
              .switch_button
                font-size 12px
                border 1px solid #ddd
                border-radius 8px
                transition background-color .3s,border-color .3s
                padding 0 6px
                width 30px
                height 16px
                line-height 16px
                color #fff
                position absolute
                top 50%
                right 10px
                transform translateY(-50%)
                &.off
                  background #fff
                  .switch_text
                    float right
                    color #ddd
                &.on
                  background #02a774
                >.switch_circle
                  //transform translateX(27px)
                  position absolute
                  top -1px
                  left -1px
                  width 16px
                  height 16px
                  border 1px solid #ddd
                  border-radius 50%
                  background #fff
                  box-shadow 0 2px 4px 0 rgba(0,0,0,.1)
                  transition transform .3s
                  &.right
                    transform translateX(30px)
            .login_hint
              margin-top 12px
              color #999
              font-size 14px
              line-height 20px
              >a
                color #02a774
          .login_submit
            display block
            width 100%
            height 42px
            margin-top 30px
            border-radius 4px
            background #4cd96f
            color #fff
            text-align center
            font-size 16px
            line-height 42px
            border 0
        .about_us
          display block
          font-size 12px
          margin-top 20px
          text-align center
          color #999
      .go_back
        position absolute
        top 5px
        left 5px
        width 30px
        height 30px
        >.iconfont
          font-size 20px
          color #999
</style>



